___CSS_LOADER_EXPORT___.push([module.id, ".loading-indicator[data-v-1dc289ee],.validation-failure[data-v-1dc289ee],.validation-pending[data-v-1dc289ee],.validation-success[data-v-1dc289ee]{position:absolute;width:100%;height:100%;background-color:hsla(0,0%,100%,.8);text-align:center;font-weight:700;font-size:1.4rem;padding:10px;display:flex;flex-flow:column nowrap;justify-content:center}.validation-success[data-v-1dc289ee]{color:green}.validation-failure[data-v-1dc289ee]{color:red}", "",{"version":3,"sources":["webpack://./resources/js/components/ui/CodeScannerModal.vue"],"names":[],"mappings":"AAmIA,mJAIA,iBAAA,CACA,UAAA,CACA,WAAA,CAEA,mCAAA,CACA,iBAAA,CACA,eAAA,CACA,gBAAA,CACA,YAAA,CAEA,YAAA,CACA,uBAAA,CACA,sBACA,CACA,qCACA,WACA,CACA,qCACA,SACA","sourcesContent":["<template>\r\n    <b-modal\r\n        ref=\"modal\"\r\n        :title=\"title\"\r\n        no-fade\r\n        hide-footer\r\n    >\r\n        <qrcode-stream\r\n            style=\"width: 100%; height: 100%;\"\r\n            :camera=\"camera\"\r\n            @init=\"onInit\"\r\n            @decode=\"onDecode\"\r\n        >\r\n            <div class=\"loading-indicator\" v-if=\"errorMessage\">\r\n                {{ errorMessage }}\r\n            </div>\r\n\r\n            <div class=\"loading-indicator\" v-if=\"loading\">\r\n                <font-awesome-icon icon=\"cog\" spin/>\r\n            </div>\r\n\r\n            <div v-if=\"validationSuccess\" class=\"validation-success\">\r\n                <font-awesome-icon icon=\"check\"/>\r\n            </div>\r\n\r\n            <div v-if=\"validationFailure\" class=\"validation-failure\">\r\n                {{ validatorMessage }}\r\n            </div>\r\n\r\n            <div v-if=\"validationPending\" class=\"validation-pending\">\r\n                <font-awesome-icon icon=\"cog\" spin/>\r\n            </div>\r\n        </qrcode-stream>\r\n    </b-modal>\r\n</template>\r\n<script>\r\nimport { BModal } from 'bootstrap-vue'\r\nimport { QrcodeStream } from 'vue-qrcode-reader'\r\nexport default {\r\n    components: {\r\n        BModal,\r\n        QrcodeStream\r\n    },\r\n    props: {\r\n        title: {\r\n            type: String,\r\n            required: false,\r\n            default: 'QR Code Scanner'\r\n        },\r\n        validator: {\r\n            type: Function,\r\n            required: false,\r\n        },\r\n        validatorMessage: {\r\n            type: String,\r\n            required: false,\r\n            default: 'Invalid input.'\r\n        }\r\n    },\r\n    data() {\r\n        return {\r\n            loading: false,\r\n            isValid: undefined,\r\n            camera: 'auto',\r\n            errorMessage: undefined\r\n        }\r\n    },\r\n    computed: {\r\n        validationPending() {\r\n            return this.isValid === undefined && this.camera === 'off'\r\n        },\r\n        validationSuccess() {\r\n            return this.isValid === true\r\n        },\r\n        validationFailure() {\r\n            return this.isValid === false\r\n        }\r\n    },\r\n    methods: {\r\n        open() {\r\n            this.$refs.modal.show()\r\n            this.turnCameraOn()\r\n        },\r\n        onInit (promise) {\r\n            this.loading = true\r\n            this.errorMessage = undefined\r\n            promise\r\n                .catch(error => this.errorMessage = error.message)\r\n                .then(this.resetValidationState)\r\n                .then(() => this.loading = false)\r\n        },\r\n        resetValidationState () {\r\n            this.isValid = undefined\r\n        },\r\n        async onDecode (content) {\r\n            this.turnCameraOff()\r\n\r\n            // Show waiting message\r\n            await this.timeout(250)\r\n\r\n            this.isValid = !this.validator || this.validator(content)\r\n\r\n            if (this.isValid) {\r\n                await this.timeout(1000)\r\n                this.$emit('decode', content)\r\n                this.resetValidationState()\r\n                this.$refs.modal.hide()\r\n                return\r\n            }\r\n\r\n            // some more delay, so users have time to read the message\r\n            await this.timeout(2500)\r\n\r\n            this.turnCameraOn()\r\n        },\r\n        turnCameraOn () {\r\n            this.camera = 'auto'\r\n        },\r\n        turnCameraOff () {\r\n            this.camera = 'off'\r\n        },\r\n        timeout (ms) {\r\n            return new Promise(resolve => {\r\n                window.setTimeout(resolve, ms)\r\n            })\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.loading-indicator,\r\n.validation-success,\r\n.validation-failure,\r\n.validation-pending {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n\r\n    background-color: rgba(255, 255, 255, .8);\r\n    text-align: center;\r\n    font-weight: bold;\r\n    font-size: 1.4rem;\r\n    padding: 10px;\r\n\r\n    display: flex;\r\n    flex-flow: column nowrap;\r\n    justify-content: center;\r\n}\r\n.validation-success {\r\n    color: green;\r\n}\r\n.validation-failure {\r\n    color: red;\r\n}\r\n</style>"],"sourceRoot":""}]);
