___CSS_LOADER_EXPORT___.push([module.id, ".clickable[data-v-710ebcbc]{cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/components/bank/person/RemarksLabel.vue"],"names":[],"mappings":"AAuFA,4BACA,cACA","sourcesContent":["<template>\r\n    <div v-if=\"remarks || allowUpdate\">\r\n        <inline-value-editor\r\n            v-if=\"form\"\r\n            :value=\"editValue\"\r\n            :placeholder=\"$t('people.remarks')\"\r\n            :disabled=\"disabled\"\r\n            :busy=\"busy\"\r\n            @submit=\"saveEdit\"\r\n            @cancel=\"cancelEdit\"\r\n        />\r\n        <template v-else>\r\n            <font-awesome-icon icon=\"comment-dots\" />\r\n            <template v-if=\"allowUpdate\">\r\n                <span\r\n                    v-if=\"remarks\"\r\n                    class=\"text-info clickable\"\r\n                    @click=\"startEdit\"\r\n                >\r\n                    {{ remarks }}\r\n                </span>\r\n                <em\r\n                    v-else @click=\"startEdit\"\r\n                    class=\"text-muted clickable\"\r\n                >\r\n                    {{ $t('people.click_to_add_remarks') }}\r\n                </em>\r\n            </template>\r\n            <span\r\n                v-else-if=\"remarks\"\r\n                class=\"text-info\"\r\n            >\r\n                {{ remarks }}\r\n            </span>\r\n        </template>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport InlineValueEditor from '@/components/ui/InlineValueEditor'\r\nimport showSnackbar from '@/snackbar'\r\nimport peopleApi from '@/api/people'\r\nexport default {\r\n    components: {\r\n        InlineValueEditor\r\n    },\r\n    props: {\r\n        person: {\r\n            required: true\r\n        },\r\n        allowUpdate: Boolean,\r\n        disabled: Boolean\r\n    },\r\n    data () {\r\n        return {\r\n            busy: false,\r\n            form: false,\r\n            remarks: this.person.remarks != null && this.person.remarks.length > 0 ? this.person.remarks : null,\r\n            editValue: ''\r\n        }\r\n    },\r\n    methods: {\r\n        startEdit () {\r\n            this.form = true\r\n            this.editValue = this.remarks\r\n        },\r\n        cancelEdit () {\r\n            this.form = false\r\n        },\r\n        async saveEdit (value) {\r\n            this.busy = true\r\n            this.editValue = value\r\n            try {\r\n                let data = await peopleApi.updateRemarks(this.person.id, value)\r\n                this.remarks = data.remarks\r\n                this.form = false\r\n                showSnackbar(data.message)\r\n            } catch (err) {\r\n                alert(this.$t('app.error_err', { err: err }))\r\n            }\r\n            this.busy = false\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.clickable {\r\n    cursor: pointer;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
