___CSS_LOADER_EXPORT___.push([module.id, ".clickable[data-v-1debff10]{cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/components/bank/person/PoliceNoLabel.vue"],"names":[],"mappings":"AA2FA,4BACA,cACA","sourcesContent":["<template>\r\n    <div v-if=\"police_no || allowUpdate\">\r\n        <inline-value-editor\r\n            ref=\"input\"\r\n            v-if=\"form\"\r\n            :value=\"editValue\"\r\n            :placeholder=\"$t('people.police_number')\"\r\n            :disabled=\"disabled\"\r\n            :busy=\"busy\"\r\n            @submit=\"saveEdit\"\r\n            @cancel=\"cancelEdit\"\r\n        />\r\n        <template v-else>\r\n            <font-awesome-icon icon=\"hashtag\" />\r\n            <template v-if=\"police_no\">\r\n                <small class=\"text-muted\">{{ $t('people.police_number') }}:</small>\r\n                <span class=\"pr-2\">\r\n                    <text-highlight :queries=\"highlightTerms\">{{ police_no }}</text-highlight>\r\n                </span>\r\n            </template>\r\n            <em\r\n                v-else-if=\"allowUpdate\"\r\n                class=\"text-muted clickable\"\r\n                @click=\"startEdit\"\r\n            >\r\n                {{ $t('people.click_to_add_police_number') }}\r\n            </em>\r\n        </template>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport InlineValueEditor from '@/components/ui/InlineValueEditor'\r\nimport TextHighlight from 'vue-text-highlight'\r\nimport showSnackbar from '@/snackbar'\r\nimport peopleApi from '@/api/people'\r\nexport default {\r\n    components: {\r\n        InlineValueEditor,\r\n        TextHighlight\r\n    },\r\n    props: {\r\n        person: {\r\n            required: true\r\n        },\r\n        allowUpdate: Boolean,\r\n        highlightTerms: {\r\n            type: Array,\r\n            required: false,\r\n            default: () => []\r\n        },\r\n        disabled: Boolean\r\n    },\r\n    data () {\r\n        return {\r\n            form: false,\r\n            busy: false,\r\n            police_no: this.person.police_no_formatted != null && this.person.police_no_formatted.length > 0 ? this.person.police_no_formatted : null,\r\n            editValue: ''\r\n        }\r\n    },\r\n    methods: {\r\n        startEdit () {\r\n            this.form = true\r\n            this.editValue = this.police_no\r\n        },\r\n        cancelEdit () {\r\n            this.form = false\r\n        },\r\n        async saveEdit (value) {\r\n            if (value == null || value.length == 0) {\r\n                this.$refs.input.focus();\r\n                return\r\n            }\r\n            this.busy = true\r\n            this.editValue = value\r\n            try {\r\n                let data = await peopleApi.updatePoliceNo(this.person.id, value)\r\n                this.police_no = data.police_no\r\n                this.form = false\r\n                showSnackbar(data.message)\r\n            } catch (err) {\r\n                alert(this.$t('app.error_err', { err: err }))\r\n            }\r\n            this.busy = false\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.clickable {\r\n    cursor: pointer;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
